.cover {
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  
  .imgAccueil {
    width: 100%;
    height: 180px;
    filter: contrast(0.5);
    border-radius: 15px;
  }
  
  .cover p {
    font-family: "montserrat";
    position: absolute;
    color: white;
    font-size: 34px;
    transform: translate(10px, 50px);
    z-index: 6;
  }
  
  .listCard {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    background-color: #F6F6F6;
    margin: 0 30px;
    border-radius: 15px;
  }
  
  .card {
    display: flex;
    flex-direction: column-reverse;
    height: 240px;
    width: 280px;
    margin: 10px 20px;
    border-radius: 15px;
    background: rgb(255, 96, 96);
    background: linear-gradient(180deg, rgb(255, 96, 96) 35%, rgba(13, 9, 12, 0.5788690476) 100%);
  }
  
  .card img {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    -o-object-fit: cover;
       object-fit: cover;
    filter: brightness(0.8);
  }
  
  .card b {
    position: absolute;
    margin: 10px;
    color: white;
    font-size: 18px;
    width: 20%;
    display: flex;
    flex-wrap: wrap;
  }
  
  @media screen and (max-width: 699px) {
    body {
      margin: 0 20px;
    }
    .imgAccueil {
      height: 100px;
    }
    .listCard {
      background-color: white;
      margin: 0 0;
    }
    .card {
      height: 100%;
      width: 100%;
      margin: 10px 0px;
    }
    .card b {
      width: 40%;
    }
    .cover {
      justify-content: flex-start;
    }
    .cover p {
      font-size: 25px;
      width: 50%;
    }
  }/*# sourceMappingURL=Home.css.map */